body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.MenuItemContent {
    padding: 8px 12px;
    color: var(--menu-item-content-color);
}

.MenuItemContent-Text {
    font-weight: bold;
}

.MenuItemContent-Hint {
    font-size: 0.7rem;
    line-height: 1rem;
    opacity: 0.7;
}

.MenuItem {
    display: block;
    cursor: default;
    outline: none;
    text-decoration: none;
    --menu-item-content-color: #000;
}

.MenuItem-SectionItems {
    /* border-left: 5px solid #a4d1ff; */
    padding-left: 12px;
}

.MenuItem-SectionTitle {
    color: rgba(0,0,0,0.7);
    text-transform: uppercase;
    font-size: 0.7rem;
    line-height: 1rem;
    padding: 0 12px;
}

.MenuItem:hover {
    background: #c6e2ff;
}

.MenuItem.MenuItem_focus {
    --menu-item-content-color: #fff;
    background: #0062c6;
}

[data-popper-reference-hidden=true] {
    visibility: hidden;
    pointer-events: none;
}
